@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: Lora;
  src: url(../assets/Lora/Lora-VariableFont_wght.ttf)
}

@font-face {
  font-family: Jost;
  src: url(../assets/Jost/Jost-VariableFont_wght.ttf)
}

.title {
  font-family: Lora;
}


.jost {
  font-family: Jost;
}

.App {
  text-align: center;
  background-color: "#C9E4CA";
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
